@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
  .Countries-items {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    grid-row-gap: 1.5em;
    display: grid;
    padding-top: 30px;
    background-color: #3B2D47;
  }
  @media only screen and (max-width: 600px) {
    .Countries-items {
        grid-template-columns: repeat(1, 1fr);
      }
  }
  .Countries-item {
    text-decoration: none;
    box-shadow: 8px 14px 38px rgba(255, 255, 255, 0.06), 1px 3px 8px rgba(251, 253, 255, 0.03);
    border-radius: 5px;
    margin: 0 0 0 0;
    position: relative;
  }
  .Countries-item-info {
    padding: 10px;
  }
  .Countries-item-info h2 {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    color: #FF4545;
  }
  .Countries-item-info p {
    font-size: 14px;
    font-weight: 300;
    text-align: center;
  }
  .Countries-item img {
    width: 100%;
    height: 50px;
    border-radius: 5px 5px 0 0;
    object-fit: contain;
  }
  .Countries-item section{
      text-align: center;
  }

  .Countries-borders {
    margin-bottom: 30px;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    grid-row-gap: 1.5em;
    display: grid;
  }


/*Desktop View*/
.header {
    display: flex;
    background: #111010;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 80px;
  }
  .logo-nav {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .logo {
    width: 100px;
    height: 80px;
    margin-right: 20px;
  }
  .nav-options {
    padding-left: 25px;
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 50px;
    list-style-type: none;
  }
  .mobile-option {
    display: none;
  }
  .option a{
    color: white;
    font-size: large;
    text-decoration: none;
    font-weight: bold;
  }
  .option :hover {
    color: white;
  }
  .mobile-menu {
    display: none;
  }
  @media (max-width: 648px) {
    /*Mobile View */
    .header {
      padding: 0px 10px;
    }
    .logo {
      width: 45px;
      height: 45px;
    }
    .nav-options {
      display: flex;
      width: 100%;
      height: 400px;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 0;
      transition: all 0.5s ease;
      flex-direction: column;
      list-style-type: none;
      grid-gap: 0px;
    }
    .nav-options.active {
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      align-content: center;
      padding-left: 0px;
    }
  
    .menu-icon {
      width: 45px;
      height: 45px;
    }
    .option {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 5vw;
      padding: 30px 0px;
    }
  }
  .mobile-menu {
    display: block;
  }

body {
